@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Anton);
* {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

body {
	font-family: 'Lato', 'Helvetica Neue', sans-serif;
	background-color: #f4f4f7;
}

/*Personalizar Scroll*/
::-webkit-scrollbar {
	background: rgba(51, 102, 147, 0.3);
	width: 10px;
}

::-webkit-scrollbar-track {
	box-shadow: inset 0 0 10px #00000070;
	border-radius: 10px;
}

::-webkit-scrollbar-thumb {
	background: rgba(0, 0, 0, 0.4);
	background: linear-gradient(125deg, #1c3643, #7dcd40);
	border-radius: 10px;
}

h1 {
	font-family: 'Anton', sans-serif;
}

a.link-unstyled {
	color: inherit;
}

a.link-unstyled:hover {
	color: inherit;
	text-decoration: none;
}

.btn {
	padding: 0.375rem 2rem;
}

.btn.btn-primary {
	color: #fff;
	background-color: #7dcd40;
	border-color: #7dcd40;
}

.btn.btn-primary:not(:disabled):not(.disabled):hover {
	color: #fff;
	background-color: #7dcd40;
	border-color: #7dcd40;
}

.btn.btn-primary:not(:disabled):not(.disabled):active {
	color: #fff;
	background-color: #7dcd40;
	border-color: #7dcd40;
}

.btn.btn-primary:not(:disabled):not(.disabled):focus {
	box-shadow: 0000.2rem rgba(125, 205, 64, 0.5);
}

.btn.btn-danger {
	color: #fff;
	background-color: #cd4040;
	border-color: #cd4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):hover {
	color: #fff;
	background-color: #cd4040;
	border-color: #cd4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):active {
	color: #fff;
	background-color: #cd4040;
	border-color: #cd4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):focus {
	box-shadow: 0000.2rem rgba(255, 64, 64, 0.5);
}

.color-white {
	color: #f4f4f7 !important;
}

.Navbar {
	width: 100%;
	padding: 0.5rem 0;
	background-color: #1c3643;
}

.Navbar__brand {
	color: #ffffff;

	display: flex;
	align-items: center;
}

.Navbar__brand:hover {
	color: #ffffff;
	text-decoration: none;
}

.Navbar__brand-logo {
	margin-right: 0.5rem;
}

.Home {
	width: 100%;
	min-height: calc(100vh - 60px);
	background: url(/react-platziBadges/static/media/stars.1d17360a.svg), #1b1b25;
	background-repeat: repeat;

	color: #ffffff;

	display: flex;
	align-items: center;
}

.Home__col {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

@media screen and (min-width: 768px) {
	.Home__col {
		align-items: flex-start;
	}
}

.BadgeNew__hero {
	width: 100%;
	padding: 2rem 0;
	background: url(/react-platziBadges/static/media/stars.1d17360a.svg), #1b1b25;
	background-repeat: repeat;
	margin-bottom: 1rem;
	color: #ffffff;
}
.BadgeNew__hero-image {
	max-height: 100px;
}

.Badge {
	background: #ffffff;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1);
	border-radius: 8px 8px 8px 8px;
	overflow: hidden;
	height: 380px;
}

.Badge__header {
	padding: 0.5rem 0;
	height: 80px;
	background: #1b1b25;
	display: flex;
	justify-content: center;
}

.Badge__section-name {
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 1rem 0;
}
.Badge__section-name h1 {
	letter-spacing: 3px;
	margin: 0;
}

.Badge__section-info {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 0.5rem 0;
	background: #f4f4f7;
}

.Badge__avatar {
	border-radius: 50%;
	margin-right: 1rem;
	width: 120px;
	height: 120px;
}

.Badge__footer {
	height: 54px;
	display: flex;
	justify-content: center;
	align-items: center;
	color: #98ca3f;
	font-weight: bold;
	font-style: italic;
}

.PageLoading {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	min-height: 100vh;
	width: 100%;
}

.lds-grid {
	display: inline-block;
	position: relative;
	width: 64px;
	height: 64px;
}

.lds-grid div {
	position: absolute;
	width: 13px;
	height: 13px;
	border-radius: 50%;
	background: #000;
	-webkit-animation: lds-grid 1.2s linear infinite;
	        animation: lds-grid 1.2s linear infinite;
}

.lds-grid div:nth-child(1) {
	top: 6px;
	left: 6px;
	-webkit-animation-delay: 0s;
	        animation-delay: 0s;
}

.lds-grid div:nth-child(2) {
	top: 6px;
	left: 26px;
	-webkit-animation-delay: -0.4s;
	        animation-delay: -0.4s;
}

.lds-grid div:nth-child(3) {
	top: 6px;
	left: 45px;
	-webkit-animation-delay: -0.8s;
	        animation-delay: -0.8s;
}

.lds-grid div:nth-child(4) {
	top: 26px;
	left: 6px;
	-webkit-animation-delay: -0.4s;
	        animation-delay: -0.4s;
}

.lds-grid div:nth-child(5) {
	top: 26px;
	left: 26px;
	-webkit-animation-delay: -0.8s;
	        animation-delay: -0.8s;
}

.lds-grid div:nth-child(6) {
	top: 26px;
	left: 45px;
	-webkit-animation-delay: -1.2s;
	        animation-delay: -1.2s;
}

.lds-grid div:nth-child(7) {
	top: 45px;
	left: 6px;
	-webkit-animation-delay: -0.8s;
	        animation-delay: -0.8s;
}

.lds-grid div:nth-child(8) {
	top: 45px;
	left: 26px;
	-webkit-animation-delay: -1.2s;
	        animation-delay: -1.2s;
}

.lds-grid div:nth-child(9) {
	top: 45px;
	left: 45px;
	-webkit-animation-delay: -1.6s;
	        animation-delay: -1.6s;
}

@-webkit-keyframes lds-grid {
	0%,
	100% {
		opacity: 1;
	}

	50% {
		opacity: 0.5;
	}
}

@keyframes lds-grid {
	0%,
	100% {
		opacity: 1;
	}

	50% {
		opacity: 0.5;
	}
}

.BadgeEdit__hero {
	width: 100%;
	padding: 2rem 0;
	background: url(/react-platziBadges/static/media/stars.1d17360a.svg), #1b1b25;
	background-repeat: repeat;
	margin-bottom: 1rem;
	color: #ffffff;
}
.BadgeEdit__hero-image {
	max-height: 100px;
}

.Badges__hero {
	width: 100%;
	padding: 2rem 0;
	background: url(/react-platziBadges/static/media/stars.1d17360a.svg), #1b1b25;
	background-repeat: repeat;
	margin-bottom: 1rem;
}

.Badges__hero > .Badges__container {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.Badges__container {
	width: 100%;
	max-width: 580px;
	margin: 0 auto;
	padding: 0 1rem;
}

.Badges_conf-logo {
	margin-bottom: 2rem;
}

.Badges__buttons {
	display: flex;
