}

.BadgeDetails__hero {
	width: 100%;
	padding: 2rem 0;
	background: url(/react-platziBadges/static/media/stars.1d17360a.svg), #1b1b25;
	background-repeat: repeat;
	margin-bottom: 1rem;
	color: #ffffff;
}

.BadgeDetails__hero-attendant-name {
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.BadgeDetails__buttons {
	display: flex;
	justify-content: flex-end;
	margin-bottom: 1rem;
}

.button-width {
	min-width: 150px;
}

.Modal {
	position: fixed;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	background-color: rgba(0, 0, 0, 0.5);

	display: flex;
	flex-direction: column;
	align-items: center;
}

.Modal__container {
	position: relative;
	top: 10rem;
	background-color: #ffffff;
	padding: 1rem;
	width: 400px;
}

.Modal__close-button {
	position: absolute;
	top: 0;
	right: 0;
	border: 0;
	background-color: #f5f5f5;
	padding: 0.5rem 1rem;
}

